  --is-background-fixed: "false";
  --is-reversed: "false";
  --folder-position: "end";
  box-sizing: border-box;
  max-width: var(--max-width, unset);
  opacity: 0;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
}

.menu-simplex :where(*) {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.menu-simplex :where(li, li > :first-child) {
  transition-property: color, background-color;
  transition-duration: .2s;
}

