  -webkit-user-select: none;
  user-select: none;
}

.menu-simplex a {
  text-decoration: none;
}

.menu-simplex .current > a {
  pointer-events: none;
}

.menu-simplex button {
  -webkit-appearance: none;
  appearance: none;
  border: inherit;
  background: inherit;
  font: inherit;
  color: inherit;
  border-radius: 0;
  padding: 0;
